@import "variables";

@media screen and(max-width: 650px), screen and (max-device-width: 650px){
  body{
    .authorization{
      width: 290px;
      .author,
      .regist{
        form{
          p{
            .user,
            .shield,
            .phone,
            .envelope{
              left: 40px;
            }
            .asterisk{
              right: 40px;
            }
          }
        }
      }
      .socialAuthor{
        ul{
          li{
            a{
              width: 125px;
            }
          }
        }
      }
      .close{
        display: block;
      }
    }
  }
  #authorizationConfirm{
    width: 290px!important;
  }
}